import React, { Component } from 'react'
import '../App.css'
import {Container, Row} from 'react-bootstrap';

import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './tops';
import Carousel from './carousel';
import Products from './products';
import Footer from './bottoms';
import CategoriesFew from './categories';

class webHome extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
                <Container fluid style={{backgroundColor:'#F9F2E7'}}>
                    <Header/>
                    <Carousel/>
                    <Container>
                        <Row style={{paddingTop:'60px', paddingLeft:'60px', paddingRight:'60px'}}>
                            <h1 style={{color:'green'}}>Kategori</h1>
                        </Row>
                    </Container>
                    <CategoriesFew/>
                    <Products/>
                    <Footer/>
                </Container>
            </React.Fragment>
         );
    }
}
 
export default webHome;