import React, { Component } from 'react';
import {Container} from 'react-bootstrap';

class apiTry extends Component {
    constructor(props){
        super(props);
        this.state = {
            posts: []
        }
    }
    componentDidMount(){
        const url = "https://jsonplaceholder.typicode.com/posts";
        fetch(url)
        .then(response => response.json())
        .then(json => this.setState({ posts: json }))
    }
    render() {
    const { posts } = this.state;
    return (
        <Container style={{backgroundColor:"#F9F2E7"}} fluid>
            <Container style={{border:"#F9F2E7 solid 1px", backgroundColor:"white"}}>
                {posts.map((post) => (
                    <li>{post.title}</li>
                    
                ))}
            </Container>
        </Container>
      
    );
  }
}
export default apiTry;