import React, { Component, Fragment } from 'react';
import { Container } from 'react-bootstrap';
import Header from './tops';
import Footer from './bottoms';
import CategoriesFew from './categories';

class allCategories extends Component {
    state = {  }
    render() { 
        return ( 
            <React.Fragment>
                <Container fluid>
                    <Header/>
                    <CategoriesFew/>
                    <Footer/>
                </Container>
            </React.Fragment>
         );
    }
}
 
export default allCategories;